ul {
  list-style: none;
}
a {
  text-decoration: none;
}

a:hover {
  color: white;
  text-decoration: none;
  cursor: pointer;
}
.support-expertise-section {
  background-color: #132a54;
  padding: 50px 0;
}
.digital-section_subtitle {
  color: #0cb340;
  font-size: 22px;
  margin-bottom: 10px;
  text-align: center;
  font-family: Orbitron;
  margin: 50px 0px;
}
.cs-section_title {
  color: #fff;
  font-size: 55px;
  text-align: center;
  font-family: Orbitron;
  margin-bottom: 30px;
}

.support-sub-heading {
  font-size: 20px;
  text-align: center;
  margin-bottom: 30px;
  font-family: Orbitron;
}
.support-sub-p {
  text-align: center;
  font-size: 18px;
  font-family: Inter;
}

.dm-iconbox_icon {
  display: block;
  margin: auto;
}
.dm-iconbox_title {
  font-size: 22px;
  margin: 20px 0 20px;
  text-align: center;
  font-family: Orbitron;
  color: #fff;
}
.dm-description {
  color: var(--text-gray);
  text-align: center;
  font-family: Inter;
}

