.light-theme .digital-marketing-section {
    background: #fff;
}
.light-theme .cs-section_title {
    color: var(--dark);
}
.light-theme .dm-iconbox_title {
    color: var(--dark);
}
.light-theme .dm-about-title {
    color: var(--dark);
}
.light-theme .dm-icon-text {
    color: var(--dark);
}
.light-theme .dm-section_title {
    color: var(--dark);
}
.light-theme .q:not(:last-child) {
    border-bottom: 1px solid #4e4e52;
}
@media only screen and (max-width: 900px) {
  .dm-about-title {
    font-size: 35px;
  }
  .dm-icon-text {
    font-size: 15px;
  }
  .cs-section_title {
    font-size: 35px;
  }
  .dm-iconbox_title {
    font-size: 25px;
  }
  .dm-section_title {
    font-size: 35px;
}
}
@media only screen and (max-width: 768px) { 
  .dm-list-section {
    justify-content: flex-start;
}
.second-list-section {
  margin-left: 50px;
}
.dm-about-image {
  width: 50vw;
}
.qna-all-section {
  height: auto;
}
.main {
  position: relative;
  margin-top: 50px;
}
}
@media only screen and (max-width: 450px) { 
  .dm-list-section {
    flex-direction: column;
  }
  .second-list-section {
    margin-left: 0px;
}
}
